* {
    margin: 0;
}

html, body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-color: dodgerblue;
}

.App {
    text-align: center;
    margin-top: 10vh;

    display: flex;
    align-items: center;
    justify-content: center;
}

.Spinner {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 3px solid rgba(255, 255, 255, 0.3);
    border-top-color: #fff;
    animation: spin 0.8s ease-in-out infinite;
}
  
@keyframes spin {
    to {
      transform: rotate(360deg);
    }
}

.CodeInput {

    width: 60vw;
    height: 60vh;

    resize: none;
}